.listbox {
  position: absolute;
  right: 0;
  left: 0;
  background: #fff;
  border: 1px solid #d1d1d1;
  box-shadow: 0 2px 4px rgb(0 0 0 / 12%);
  border-radius: 4px;
  padding-top: 8px;
  padding-bottom: 8px;
  top: calc(100% + 4px);
  z-index: 1;

}

.listbox__list {
  padding-left: 0;
  list-style: none;
  margin: 0;
  width: 100%;
}

.listbox__item {
  color: #111;
  padding: 8px 16px;
  line-height: 1.4;
  max-width: 100%;
  cursor: pointer;
}

.listbox__item[aria-selected="true"] {
  background: #eee
}

.form {
  position: relative;
  border-radius: 4px;
  background-color: #f5f5f5;
  height: 54px;
  display: flex;
  transition: all .1s ease-in-out;
  margin-top: 20px;
}

.form:focus-within {
  background: #fff;
  box-shadow: 0 0 0 5px rgba(0 0 0 / 20%)
}

.search {
  cursor: pointer;
  display: flex;
  align-items: center;
  box-shadow: none;
  padding: 0;
  background: none;
  border: none;
  text-align: inherit;
}

.search__svg {
  width: 24px;
  height: 24px;
  padding: 0 14px;
  fill: #767676;
  transition: fill .1s ease-in-out;

  &:hover {
    fill: #000000;
  }
}

.combobox {
  display: flex;
  flex-grow: 1;
}

.input {
  box-shadow: none;
  outline: none;
  padding-left: 12px;
  flex-grow: 1;
  width: 100%;
  background: none;
  border: none;
  color: #111;
  outline-offset: -2px;
}

.reset {
  display: flex;
  align-items: center;
  box-shadow: none;
  padding: 0;
  border: none;
  text-align: inherit;
  background-color: initial;
  cursor: pointer;
  color: inherit;
}

.reset[hidden] {
  display: none
}

.reset__svg {
  width: 20px;
  height: 20px;
  padding-left: 14px;
  padding-right: 14px;
  fill: #767676;
  transition: fill .1s ease-in-out;

  &:hover {
    fill: #000000;
  }
}
